import Typography from "typography";
import Wordpress2016 from "typography-theme-wordpress-2016";

const siteTitleFont = "Neucha";
const siteTitleFullFont = [siteTitleFont, "cursive"];

const bodyFont = "Merriweather";
const bodyFullFont = [bodyFont, "Georgia", "serif"];

Wordpress2016.overrideThemeStyles = () => {
  return {
    "a.gatsby-resp-image-link": {
      boxShadow: `none`,
    },
    ".site-title": {
      fontFamily: siteTitleFullFont.join(","),
    },
  };
};

Wordpress2016.googleFonts = [
  {
    name: siteTitleFont,
    styles: [],
  },
  {
    name: bodyFont,
    styles: ["400", "400i", "700", "700i", "900", "900i"],
  },
];

Wordpress2016.headerFontFamily = bodyFullFont;
Wordpress2016.bodyFontFamily = bodyFullFont;

const typography = new Typography(Wordpress2016);

// Hot reload typography in development.
if (process.env.NODE_ENV !== `production`) {
  typography.injectStyles();
}

export default typography;
export const rhythm = typography.rhythm;
export const scale = typography.scale;
